.App {
  text-align: center;
  background-color: #1a252f;
  font-size: large;

}

.App-header {
  padding-top: 15vh;
  padding-bottom: 15vh;
  background-image: url("../public/assets/duck-banner.jpg");
  min-height: 10vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  border-bottom: 2px solid black;
  background-size: cover;
}

.App-link {
  color: #61dafb;
}

.Section-primary {
  padding-top: 75px;
  padding-bottom: 75px;
  background-image: url("../public/assets/duck-continue.jpg");
  background-color: #ccf4fe;
  color: #212529;
  border-bottom: 2px solid black;
  background-size: cover;
}

.Section-secondary {
  padding-top: 75px;
  padding-bottom: 75px;
  background-color: #ffffff;
  color: #212529;
  border-bottom: 2px solid black;
}

.Section-footer {
  padding-top: 75px;
  padding-bottom: 75px;
  background-color: #2c3e50;
  color: #ffffff;
}

.Bg-primary {
  background-color: #2c3e50;
}

.Bg-secondary {
  background-color: #ffffff;
}

.Footer {
  color: #ffffff;
  background-color: #1a252f;
  position: absolute;
  width: 100%;
  margin-bottom: 0px;
}

.Card {
  margin: auto;
  width: 300px;
  height: auto;
  padding-top: 10px;
  padding-bottom: 50px;
  padding-right: 2rem;
  padding-left: 2rem;
  margin-top: 50px;
  margin-bottom: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 1rem;
  box-shadow: 10px 10px 10px 10px #888888;
}

.Figure {
  margin-right: 0px;
  margin-left: 0px;
}

.Figure-image {
  border: 3px solid black;
  border-radius: 1rem;
  box-shadow: 3px 3px 3px 3px #888888;
}

.Figure-image:hover {
  transform: scale(1.10);
  box-shadow: 0px 0px #888888;
}

.Content {
  margin-top: 5%;
  margin-bottom: 5%;
  background-color: #ffffff;
  min-height: 10vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.Container {
  margin-left: 10%;
  margin-right: 10%;
  padding: 2rem;
}

.Content-container {
  border: 2px solid black;
  border-radius: 1rem;
  background-color: white;
  padding-bottom: 5rem;
  padding-right: 5rem;
  padding-left: 5rem;
}

.Display {
  border: 3px solid rgba(128, 128, 128, 0.315);
  background-color: #ffffff;
  border-radius: 2rem;
  padding: 50px;
}

.Figure-caption {
  margin-top: 3px;
  margin-bottom: 5px;
}

.Col {
  flex: 1 0 0%;
}

.Row {
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * 0.5rem);
}

.Divider-custom-line {
  width: 100%;
  max-width: 7rem;
  height: 0.25rem;
  background-color: black;
  border-radius: 1rem;
  border-color: black;
  margin-left: 20px;
  margin-right: 20px;
}

.Divider-custom {
  margin: 1.25rem 0 1.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Title {
  margin-bottom: 0px;
}

.pt-10 {
  padding-top: 10px;
}

.pb-10 {
  padding-bottom: 10px;
}

.Navbar-fixed {
  background-color: #303c54;
  width: 100%;
  height: 6rem;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  color: #fffffd;

}

.Navbar-title {
  text-align: left;
  padding-left: 15rem;
  margin: 0.5rem;

}

.Header-link {
  margin: 5px;
  border-radius: 100%;
  display: inline-flex;
  width: 3.25rem;
  height: 3.25rem;
  font-size: 1.25rem;
  justify-content: center;
  align-items: center;
  border: 1px solid #ffffff;
}



